.sectionheaderContact {
    padding-top: 3%;
    padding-bottom: 0;
    text-align: center;
    position: relative;
    font-size: 4rem;
    color: #000000;
}

@media only screen and (max-width: 1370px) {
    .sectionheaderContact {
        margin-top: 1%;
        font-size: 3rem;
        background-color: #000000;
        border-radius: 20rem;
        padding: 0.30rem 0.30rem 0.30rem;
        color: #ffffff;

    }
}

.sectionsubheaderContact {
    padding-top: 0;
    text-align: center;
    left: 8rem;
    position: inherit;
    font-size: 2rem;
    color: #000000;
    padding-bottom: 3%;
}

@media screen and (max-width: 900px) {
    .sectionsubheaderContact {
        font-size: 1.25rem !important;
        left: 6rem;
        font-style: normal;
    }
}

.contactcardouter {
    background-color: #ffffff;
    height: 30rem;
    box-shadow: 0px 6px 67.15px 11.85px rgba(0, 0, 0, 0.6);
}

@media only screen and (max-width:1200px) {
    .contactcardouter {
        height: 23rem;
    }
}

@media only screen and (max-width: 900px) {
    .contactcardouter {
        height: 13rem;
    }
}

@media only screen and (max-width: 600px) {
    .contactcardouter {
        height: 11rem;
    }
}

.content {
    padding-top: 5%;
    color: #000000;
    font-size: 2.0rem;
    text-align: center;
}

@media only screen and (max-width: 900px) {
    .content {
        font-size: 1.25rem !important;
        left: 6rem;
        font-style: normal;
        text-decoration: underline;
    }
}

.contactbyphone {
    padding-top: 5%;
    text-align-last: center;
    text-decoration: underline;
}

.contactbyphone:hover {
    padding-top: 5%;
    text-align-last: center;
    text-decoration: underline;
    text-decoration-color:	#0000FF !important;
    color: #000000 !important;
    filter: drop-shadow(0rem 0.5rem 0.5rem 	#0000FF);
}

@media only screen and (max-width: 900px) {
    .contactbyphone {
        font-size: 1.25rem !important;
        left: 6rem;
        font-style: normal;
        text-decoration: underline;
        text-decoration-color: #000000 !important;
    }
}

@media only screen and (max-width: 900px) and (min-width: 560px) {
    .contactbyphone {
        margin-right: 50%;
    }
}

.contactphonelogo {
    padding-top: 5%;
    text-align: center;
    width: 50%;
    height: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;

}

@media only screen and (max-width: 900px) {
    .contactphonelogo {
        display: none;
    }
}

.contactbyemail {
    padding-top: 5%;
    text-align-last: center;
    text-decoration: underline;
    text-decoration-color: #000000 !important;
    color: #000000 !important;
}

.contactbyemail:hover {
    padding-top: 5%;
    text-align-last: center;
    text-decoration: underline;
    text-decoration-color: 	#0000FF !important;
    color: #000000 !important;
    filter: drop-shadow(0rem 0.5rem 0.5rem #0000FF);
}

@media only screen and (max-width: 900px) {
    .contactbyemail {
        font-size: 1.25rem !important;
        font-style: normal;
        text-decoration: underline;
    }
}

@media only screen and (max-width: 900px) and (min-width: 560px) {
    .contactbyemail {
        margin-right: 50%;
    }
}

.contactbyemaillogo {
    padding-top: 5%;
    text-align: center;
    width: 50%;
    height: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

@media only screen and (max-width: 900px) {
    .contactbyemaillogo {
        display: none;
    }
}