.contain {
    position: relative;
  }
  .overlay {
    position: absolute;
    display: block;
    margin-left: auto;
    margin-right: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: auto;
    width: 90%;
    opacity: 0;
    transition: .5s ease;
  }
  .contain:hover .overlay {
    opacity: 0.9;
    background-color: #000000;
  }
  .text {
    color:#ffffff;
    font-size: 1rem;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
    z-index: 999;
  }
  @media screen and (max-width: 1200px) and (min-width: 770px) {
    .contain:hover .overlay {
        opacity: 0.9;
        background-color: #000000;
        height: 75%;
        z-index: 999;
        font-size: 0.75rem;
        display: flex;
      }
}
@media screen and (max-width: 1200px) and (min-width: 770px){
    .text {
        top:40%;
        left: 50%;
      }
}